[{title: 'Subject' , text: "Votre demande de d'enregistrement de vehicule du xxxdatexxx (xxxcasexxx)"},{title: 'Opener (Female)' , text: "Cher Madame xxxnamexxx, \n\nNous faisons suite à votre demande du xxxdatexxx qui a retenu toute notre attention."},{title: 'Opener (Male)' , text: "Cher Monsieur xxxnamexxx, \n\nNous faisons suite à votre demande du xxxdatexxx qui a retenu toute notre attention."},{title: 'Closer (Female)' , text: "Comptant sur votre compréhension, nous vous prions d’agréer, Madame xxxnamexxx, l’expression de nos salutations distinguées.\n\nCordialement,"},{title: 'Closer (Male)' , text: "Comptant sur votre compréhension, nous vous prions d’agréer, Monsieur xxxnamexxx, l’expression de nos salutations distinguées.\n\nCordialement,"},{title: 'Delayed response' , text: "Nous vous prions de bien vouloir accepter nos excuses pour le délai d'attente concernant notre réponse. Nous vous remercions pour votre patience, et sommes sincèrement navrés pour la gêne occasionnée."},{title: 'Rejected: Missing Documents' , text: "Nous avons reçu vos documents pour l’ajout de votre nouveau véhicule sur votre profil My Porsche et sommes dans le regret de vous informer que nous ne pouvons donner une suite favorable à votre demande, car certains documents sont manquants.\n\nXXXDOCUMENTSXXX\n\nPar souci de sécurité et pour la protection de vos données personnelles, nous vous remercions de bien vouloir soumettre ces documents à travers le portail My Porsche.\n\nDès que vos données auront été vérifiées avec succès, nous vous en informerons par e-mail dans lequel figurera un lien. En cliquant sur celui-ci, votre véhicule sera ajouté à votre compte My Porsche."},{title: 'Missing document: ID' , text: "Nous vous remercions de bien vouloir nous transmettre pour justificatif d’identité, l’un des documents suivants :\n\n− Carte d’identité en cours de validité recto-verso\n\n− Carte de séjour en cours de validité recto-verso\n\n− Passeport en cours de validité recto-verso"},{title: 'Missing document: Vehicle' , text: "Nous aurions également besoin pour preuve de propriété, l’un des documents suivants en français:\n\n- Certificat de cession\n\n- Carte grise\n\n- facture d’achat\n\nDans lequel seront mentionnés votre nom et le numéro d’identification du véhicule (numéro de châssis). Si vous n’êtes pas le propriétaire du véhicule, nous vous remercions de bien vouloir prendre contact auprès du propriétaire pour obtenir une procuration."},{title: 'Missing document: Authorization (company)' , text: "En effet, le permis de circulation est au nom de xxxcompanyxnamexxx; s'il s'agit d'une compagnie, nous vous remercions de bien vouloir nous transmettre une procuration dans laquelle seront mentionnés:\n\n- le nom de la compagnie\n\n- l'adresse de la compagnie\n\n- votre nom et prénom\n\n- numéro d'identification du véhicule (numéro de châssis)\n\n- nom et signature du responsable\n\n- et/ou KBIS, si vous êtes désigner comme gérant sur ce dernier \n\nSi vous ne pouvez pas soumettre ce document, nous vous invitons à vous rapprocher de votre Centre Porsche."},{title: 'Missing document: Authorization (individual)' , text: "En effet, les documents du véhicule sont au nom de xxxmonsieurxmadamexxx xxxnamexdocumentxxx, et le compte client est au nom de xxxmonsieurxmadamexxx xxxnamexformxxx.\n\nAfin de valider votre demande, tous les documents soumis doivent être au même nom. Si vous n’êtes pas le propriétaire du véhicule, nous vous remercions de bien vouloir prendre contact auprès du propriétaire pour obtenir une procuration dans laquelle devra figurer : \n\n- nom complet du propriétaire\n\n- date de naissance du propriétaire\n\n- numéro d'identification du véhicule\n\n- nom complet de l'utilisateur du véhicule\n\n- date de naissance de l'utilisateur\n\n- signature du propriétaire"},{title: 'Rejected: Information' , text: "Vous nous avez contactés pour demander l'enregistrement de votre véhicule sur votre compte My Porsche. Afin que nous puissions le mener à bien, nous vous demandons de répondre à ce courriel en fournissant les informations suivantes:\n\nXXXINFORMATIONXXX"},{title: 'Info: Different Dealer ID (VIN linked)' , text: "- Nous avons vérifié que votre véhicule est immatriculé à votre nom au Centre Porsche xxxpcxvehiclexxx. Toutefois, dans le formulaire que vous nous avez envoyé, vous avez choisi le Centre Porsche xxxpcxformxxx comme préférence. Malheureusement, vous devrez contacter directement votre nouveau Centre Porsche pour effectuer ce changement. Nous pouvons également compléter l'inscription auprès du Centre Porsche d'origine. Pour cela, nous avons besoin de votre confirmation."},{title: 'Info: Different Dealer ID (VIN not linked)' , text: "- Vous avez sélectionné le Centre Porsche xxxcpxformxxx dans votre formulaire d'inscription, mais nous avons pu vérifier qu'il existe un profil avec le Centre Porsche xxxbpxxx. Il nous faudrait confirmer avec vous le centre Porsche auquel votre véhicule doit être enregistré."},{title: 'Info: Different address' , text: "-En effet, nous ne sommes pas en mesure de vérifier l'adresse postale figurant sur le formulaire d'enregistrement car elle est différente sur les documents soumis. Nous vous remercions de bien vouloir modifier l'adresse sur votre profil My Porsche par l'une figurant sur un des documents. Ensuite, nous aurions besoin que vous soumettezsoumettiez une nouvelle demande d'enregistrement sur My Porsche, en prenant en compte la nouvelle adresse.\n\nDès que vos données auront été vérifiées avec succès, nous vous en informerons par e-mail dans lequel figurera un lien. En cliquant sur celui-ci, votre véhicule sera ajouté à votre compte My Porsche. Vous pourrez, enfin, changer l'adresse sur votre profil par celle désirée."},{title: 'PC Notification: Subject' , text: "Enregistrement d´un nouveau véhicule au sein de votre Centre Porsche"},{title: 'PC Notification: Email (New BP)' , text: "Mesdames et Messieurs,\n\nNous souhaitons vous informer qu'un nouveau Business Partner a été créé pour votre Centre Porsche dans C@P.\n\nID BP: xxxbpxxx\nNom BP: xxxmssrxmmexxx xxxfirstxnamexxx xxxlastxnamexxx\n\nCe client s’est enregistré sur My Porsche et vous a sélectionné en tant que Centre Porsche principal.\n\nDans My Porsche, le client a accepté de partager ses données personnelles avec votre Centre Porsche. Nous vous saurions gré de bien vouloir le contacter afin de l’accueillir en Centre Porsche et recueillir son consentement marketing par courrier ou e-mail.\n\nNous vous remercions et vous souhaitons une agréable journée.\n\nSincères salutations,"},{title: 'PC Notification: Email (VIN only)' , text: "Mesdames et Messieurs,\n\nLe client xxxmssrxmmexxx xxxfirstxnamexxx xxxlastxnamexxx [xxxbpxxx] s’est inscrit sur My Porsche en autonomie et vous a sélectionné en tant que Centre Porsche principal.\n\nAprès vérification des documents soumis, nous avons créé la relation entre le BP et le VIN dans C@P.\n\nNous vous saurions gré de bien vouloir le contacter afin de l’accueillir en Centre Porsche et recueillir son consentement marketing parcourrier ou e-mail.\n\nNous vous remercions et vous souhaitons une agréable journée.\n\nSincères salutations,"},{title: 'Documentation: PoO for VIN' , text: "INNSO xxxinnsoxxx\nPoO for VIN: xxxvinxxx\n\nMarket BP: xxxbpxxx > PC xxxpcxxx\n\nID:\n-First Name:\n-Last Name:\n-Date of Birth:\n-Address:\n-Validity:\n\nVehicle:\n-VIN:\n-First Name:\n-Last Name:\n-Address: \n-Registration Date:\n\nAuthorization:\n-Company/Owner Name:\n-Signed by:\n-VIN:\n-Auth. Driver First Name:\n-Auth. Driver Last Name:\n\nForm:\n-First Name:\n-Last Name:\n-Porsche ID:\n-Mobile:\n-Address:\n-Dealer ID:\n\n\nNotes: \n#-->: [xxxnamexxx] xxxdatexxx:"},{title: 'Documentation: VIN already linked to BP' , text: "INNSO xxxinnsoxxx\nVIN already linked to BP.\n\nMarket BP: xxxbpxxx > PC xxxpcxxx\n\nForm:\n-First Name:\n-Last Name:\n-Porsche ID:\n-Mobile:\n-Dealer ID: \n\n#-->: [xxxnamexxx] xxxdatexxx:"},{title: 'Denied: Non-Supported Country (Full)' , text: "Nous apprécions votre intérêt et votre inscription sur notre portail My Porsche.\n\nNous avons reçu vos documents pour la vérification de la preuve de propriété de votre véhicule. Malheureusement, nous ne sommes pas en mesure de traiter la vérification de vos documents à ce stade.\n\nPar conséquent, nous vous recommandons de contacter un Centre Porsche de votre choix qui pourra vous aider. Vous trouverez un centre Porsche dans votre région sous le lien suivant : https://www.porsche.com/pap/dealersearch/\n\nNous nous excusons pour tout inconvénient causé et nous vous remercions de votre compréhension.\n\nSi vous avez d’autres questions, n’hésitez pas à nous contacter.\n\nCordialement,"}]
