[{title: 'Subject' , text: "Su pedido de registro de vehículo del xxxfechaxxx (xxxcasexxx)"},{title: 'Opener (M)' , text: "Estimado Sr. xxxapellidoxxx,\n\nEn primer lugar, queremos agradecerle su solicitud para agregar su vehículo a My Porsche del xxxdiaxx de xxxmesxxx de xxxañoxxx."},{title: 'Opener (F)' , text: "Estimada Sra. xxxapellidoxxx,\n\nEn primer lugar, queremos agradecerle su solicitud para agregar su vehículo a My Porsche del xxxdiaxx de xxxmesxxx de xxxañoxxx."},{title: 'Rejected: Docs (full)' , text: "XXXOPENERXXX\n\nPara que podamos completarlo con éxito, quisiéramos pedirle que nos enviase los siguientes documentos:\n\nXXXDOCUMENTOSXXX\n \nPara proteger su información personal, cargue el documento a través del formulario de registro [https://connect-portal.porsche.com/core/es/es_ES/vehicles/add] ya que esto asegurará la transmisión encriptada. \n\nTan pronto como sus documentos estén completos y su activación para My Porsche se haya completado con éxito, se lo informaremos inmediatamente por correo electrónico. Si tiene cualquier otra duda o consulta, por favor no dude en contactarnos. \n \nReciba un cordial saludo,\nSu equipo Porsche."},{title: 'Rejected: Info (full)' , text: "XXXOPENERXXX\n\nPara que podamos completarlo con éxito, quisiéramos pedirle que responda a este correo electrónico con la siguiente información:\n\nXXXINFORMACIONXXX\n\nTan pronto como esta información adicional sea procesada y su activación para My Porsche se haya completado con éxito, se lo informaremos inmediatamente por correo electrónico. Si tiene cualquier otra duda o consulta, por favor no dude en contactarnos. \n \nReciba un cordial saludo,\nSu equipo Porsche."},{title: 'Rejected: Docs + Info (full)' , text: "XXXOPENERXXX\n\nPara que podamos completarlo con éxito, quisiéramos pedirle que enviase los siguientes documentos:\n\nXXXDOCUMENTOSXXX\n\nPara proteger su información personal, cargue el documento a través del formulario de registro [https://connect-portal.porsche.com/core/es/es_ES/vehicles/add] ya que esto asegurará la transmisión encriptada. \n\nQuisiéramos pedirle también que responda a este correo electrónico con la siguiente información:\n\nXXXINFORMACIONXXX\n\nTan pronto como estos datos sean procesados y su activación para My Porsche se haya completado con éxito, se lo informaremos inmediatamente por correo electrónico. Si tiene cualquier otra duda o consulta, por favor no dude en contactarnos.\n\nReciba un cordial saludo,\nSu equipo Porsche."},{title: 'Denied: Not owner (full)' , text: "XXXOPENERXXX\n\nSin embargo no fue posible completar el proceso por las siguientes razones:\n\n1. El pedido debe ser sometido desde la cuenta del propietario registrado del vehículo.\n\n2. Los datos en el formulario deben coincidir con la documentación enviada.\n\nSi la información de propietad no está actualizada, le rogamos que actualize la documentación antes de realizar nuevo pedido y en el caso de que no sea titular del vehículo, se podrá registrar como usuario secundario mediante una invitación enviada desde la cuenta de My Porsche del usuario principal.\n\nEn caso de que tenga alguna futura consulta, no dude en ponerse en contacto con nosotros.\n\nReciba un cordial saludo,\nSu equipo Porsche."},{title: 'Denied: Non-Supported Country' , text: "XXXOPENERXXX\n\nDesafortunadamente, no podemos procesar la verificación de sus documentos en esta etapa.\n\nPor lo tanto, le recomendamos que se ponga en contacto con un Centro Porsche de su elección para completar su registro. Siga el siguiente enlace para identificar el Centro Porsche que más le convenga:\n\n[https://www.porsche.com/spain/dealersearch/]\n\nPedimos disculpas por las molestias causadas y agradecemos su comprensión.\n\nEn caso de que tenga alguna futura consulta, no dude en ponerse en contacto con nosotros.\n\nReciba un cordial saludo,\nSu equipo Porsche."},{title: 'Missing document: ID' , text: "#. Documento de identidad: El lado anverso del DNI del conductor que se identifica en el formulario, con todos los datos en el mismo visibles."},{title: 'Missing document: Vehicle' , text: "#. Permiso de Circulación: El documento debe tener visible el VIN (número de bastidor) y el nombre del propietario. Si usted no es el propietario, por favor adjunte una autorización de la entidad propietaria del vehículo."},{title: 'Missing document: Authorization' , text: "#. Documento de autorización: Si el vehículo está en nombre de empresa, necesitamos un documento que autorice al conductor que se identifica en este proceso. Debe contener el VIN del vehículo, nombre y apellido del conductor, nombre de la empresa y las firmas de ambas partes."},{title: 'PC notification: Description' , text: "Smart Mobility - Nuevo Proprietario"},{title: 'PC notification: Notes (BP+VIN)' , text: "Estimado Centro Porsche,\n\nSe ha registrado un nuevo cliente a través del Portal My Porsche para su concesión.\n\nNº de BP : xxxbpxxx\nNº VIN: xxxvinxxx\n \nLe rogamos que realice las siguientes tareas:\n  1. Asignar un comercial y asesor responsable.\n  2. Revisar los datos del cliente en C@P.\n  3. Contactar con el cliente para darle la bienvenida a su Centro Porsche y presentarle su asesor/comercial responsable.\n\nEn el portal My Porsche el cliente ha aceptado ser contactado por su Centro Porsche inicialmente por correo postal o correo electrónico.\n \nSaludos cordiales,\nSu equipo Porsche."},{title: 'PC notification: Notes (VIN only)' , text: "Estimado Centro Porsche,\n\nUn cliente de su concesión ha registrado su vehículo a través del portal My Porsche. Hemos creado la relación con el vehículo después de comprobar los documentos presentados.\n\nNº de BP : xxxbpxxx\nNº VIN: xxxvinxxx\n\nLe rogamos que realice las siguientes tareas:\n\n  1. Asignar un comercial y asesor responsable\n  2. Revisar los datos del cliente en C@P\n  3. Contactar con el cliente para darle la bienvenida a su Centro Porsche y presentarle su asesor /comercial responsable (si necesario).\n\nEn el portal My Porsche el cliente ha aceptado ser contactado por su Centro Porsche inicialmente por correo postal o correo electrónico.\n\nSaludos cordiales,\nSu equipo Porsche."},{title: 'PC notification: CRM Managers' , text: "ppn.porsche.com/portal/docs/DOC-123296"},{title: 'Documentation: PoO for VIN' , text: "INNSO xxxinnsoxxx\nPoO for VIN: xxxvinxxx\n\nMarket BP: xxxbpxxx > PC xxxpcxxx\n\nID:\n-First Name:\n-Last Name:\n-Date of Birth:\n-Validity:\n-ID Number:\n\nVehicle:\n-VIN:\n-First Name:\n-Last Name:\n-Address: \n-Registration Date:\n\nVehicle Status: (New/CPO/Used/Private market) and why?\n>\n\nAuthorization:\n-Company Name:\n-Signed by:\n-VIN:\n-Auth. Driver First Name:\n-Auth. Driver Last Name:\n\nForm:\n-First Name:\n-Last Name:\n-Porsche ID:\n-Mobile:\n-Address:\n-Dealer ID:\n\n\nNotes: \n#-->: [xxxnamexxx] xxxdatexxx:"},{title: 'Documentation: VIN already linked to BP' , text: "INNSO xxxinnsoxxx\nVIN already linked to BP.\n\nMarket BP: xxxbpxxx > PC xxxpcxxx\n\nForm:\n-First Name:\n-Last Name:\n-Porsche ID:\n-Mobile:\n-Dealer ID: \n\n#-->: [xxxnamexxx] xxxdatexxx:"},{title: 'Market Specific Workflow' , text: "https://ppn.porsche.com/portal/servlet/JiveServlet/download/123296-20-499388/20201104_Self-Registration_PoO_ES_PT.pptx"}]
